* {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

a {
  text-decoration: none;
  color: black;
}

a:hover {
  color: brown;
  background: rgb(59, 160, 167);
  border-radius: 5px;
}

/* ul {
  display: flex;
  justify-content: center;
} */

body {
  background: linear-gradient(
    to top,
    rgb(27, 41, 46) 0%,
    rgb(97, 121, 125) 30%,
    rgb(136, 169, 187) 70%,
    rgb(202, 241, 255) 100%
  );
  background-attachment: fixed;
  /* display: flex; */
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

section {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  padding: 0px;
}

.item-list {
  display: flex;
  flex-wrap: wrap;
  width: 100vw;
  padding: 0px 20px;
  margin: 0%;
}

.item {
  background: linear-gradient(
    to top,
    rgb(194, 45, 0) 0%,
    rgb(196, 75, 10) 10%,
    rgb(244, 172, 134) 65%,
    rgb(255, 240, 215) 100%
  );
  border: 1px solid rgb(255, 172, 136);
  margin: 15px;
  padding: 30px;
  max-width: 200px;
  list-style-type: none;
  border-radius: 10px;
  box-shadow: -5px 5px 10px 0px black;
}

.item img {
  max-width: 200px;
  border-radius: 10px;
}


.list-section {
  margin-top: 180px;
}




.display-by {
  float: right;
  /* width: 50vw; */
  padding: 20px 20px 20px 20px;
}
.comp-box {
  border: 2px solid red;
} 

.fixed-position {
  overflow: hidden;
  position: fixed;
  top: 5px;
  width: 90%;
}

.navbar {
  background-color: grey;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}

.nav-menu {
  background-color: grey;
  width: 250px;
  height: 60vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 10px;
  left: -100%;
  transition: 550ms;
}

.nav-menu.active {
  left: 0;
  transition: 250ms;
  background-color: gray;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: white;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: blue;
}

.nav-menu-items {
  width: 100%;
  background-color: gray;

}

.navbar-toggle {
  background-color: rgb(159, 50, 50);
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

span {
  margin-left: 16px;
}
















.category-overall {
  border: 1px solid red;
  margin: 30%;
}

ul li {
  list-style: none;
}
.chosen-review {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  padding: 15vh 10vw;
}

.chosen-title {
  background: white;
}

.single-item-image {
  margin-top: 10px;
  width: 80vw;
  border-radius: 3px;
  border: 5px solid antiquewhite;
}

.review-body {
  background: white;
  justify-content: center;
}

.bottom-bar {
  background: black;
  opacity: 1;
  border: 2px solid red;
  overflow: hidden;
  position: fixed;
  bottom: 5px;
  width: 90%;
}

