

.display-by {
  float: right;
  /* width: 50vw; */
  padding: 20px 20px 20px 20px;
}
.comp-box {
  border: 2px solid red;
} 

.fixed-position {
  overflow: hidden;
  position: fixed;
  top: 5px;
  width: 90%;
}

.navbar {
  background-color: grey;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}

.nav-menu {
  background-color: grey;
  width: 250px;
  height: 60vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 10px;
  left: -100%;
  transition: 550ms;
}

.nav-menu.active {
  left: 0;
  transition: 250ms;
  background-color: gray;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text a {
  text-decoration: none;
  color: white;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text a:hover {
  background-color: blue;
}

.nav-menu-items {
  width: 100%;
  background-color: gray;

}

.navbar-toggle {
  background-color: rgb(159, 50, 50);
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}

span {
  margin-left: 16px;
}















